<template>
  <div class="settings">
    <div class="row">
      <div class="flex md12 lg6">
        <DepositsRequests :loading="loading" :value="requests" />
      </div>
      <div class="flex md12 lg6">
        <DepositsVerified :loading="loading" :value="verified" />
      </div>
    </div>
  </div>
</template>

<script>
import DepositsRequests from './DepositsRequests'
import DepositsVerified from './DepositsVerified'
import { GetAllDeposits } from '../../services/Transactions'

export default {
  name: 'Deposits',
  components: { DepositsRequests, DepositsVerified },
  data: () => ({
    loading: false,
    deposits: [],
  }),
  computed: {
    requests() {
      return this.deposits.filter(d => d.status == 'processing')
    },
    verified() {
      return this.deposits.filter(d => d.status == 'success')
    },
  },
  methods: {
    updateDeposits() {
      this.loading = true
      GetAllDeposits()
        .then(({ data: deposits }) => {
          this.loading = false
          this.deposits = deposits
        })
        .catch(() => {
          this.loading = false
        })
    },
  },
  mounted() {
    this.$root.depositsInterval = setInterval(this.updateDeposits, 30000)
    this.updateDeposits()
  },
  destroyed() {
    clearInterval(this.$root.depositsInterval)
  },
}
</script>

